"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";

export default function AskPage() {
  const router = useRouter();
  const [noButtonPosition, setNoButtonPosition] = useState({ x: 50, y: 50 });
  const [windowSize, setWindowSize] = useState({ width: 900, height: 600 });

  useEffect(() => {
    // Initialize window size
    setWindowSize({
      width: window.innerWidth || 900,
      height: window.innerHeight || 600,
    });

    // Update window size on resize
    const handleResize = () => {
      setWindowSize({
        width: window.innerWidth,
        height: window.innerHeight,
      });
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const moveNoButton = () => {
    const maxX = Math.max(0, windowSize.width - 200); // 200px is button width
    const maxY = Math.max(0, windowSize.height - 60); // 60px is button height
    const newX = Math.random() * maxX;
    const newY = Math.random() * maxY;
    setNoButtonPosition({ x: newX, y: newY });
  };

  const handleYesClick = () => {
    router.push('/schedule');
  };

  return (
    <main className="min-h-screen flex flex-col items-center justify-center p-8 bg-[#fdf4f5] relative overflow-hidden">
      <div className="text-center space-y-12 relative z-10">
        <h1 className="text-4xl font-bold text-[#333]">
          Dwi Nurandina Rizal Basirr mau gaa kamu jadii pacarr akuuu???ü§çü§ç
        </h1>
        
        <div className="flex flex-col items-center gap-6 relative min-h-[200px]">
          <button 
            onClick={handleYesClick}
            className="inline-block px-8 py-4 bg-[#f6a5c0] text-white rounded-lg 
                     font-medium shadow-lg hover:shadow-xl transform hover:scale-105 
                     transition-all duration-300 min-w-[200px] z-20"
          >
            Iyaa mauu jaa
          </button>
          
          <button
            className="px-8 py-4 bg-gray-400 text-white rounded-lg font-medium 
                     shadow-lg transition-all duration-300 min-w-[200px]
                     absolute z-10"
            style={{
              left: noButtonPosition.x,
              top: noButtonPosition.y,
            }}
            onMouseEnter={moveNoButton}
          >
            GAAA GAMAUUUUU
          </button>
        </div>
      </div>
    </main>
  );
}
